op=data.data.top;\r\n                    self.good=data.data.good;\r\n                    self.tab=data.data.tab;\r\n                    self.name=data.data.author.loginname;\r\n                    self.img=data.data.author.avatar_url;\r\n                    self.replies=data.data.replies.length;\r\n                    self.arrs=data.data.replies;\r\n\r\n                    self.atBottom=false;\r\n                }\r\n            })\r\n        }\r\n    }\r\n</script>\r\n\r\n\r\n<style scoped>\r\n\r\n    .loadmore{\r\n        margin-top: 80px;\r\n    }\r\n\r\n    .allDetail{\r\n        \r\n    }\r\n\r\n    .topic-title,.section{\r\n        padding: 10px;\r\n    }\r\n    .topic-title{\r\n        margin-top: 50px;\r\n    }\r\n    \r\n    .markdown-body{\r\n        padding: 10px;\r\n        color: #333;\r\n        font-weight: 300;\r\n        font-size: 16px;\r\n        line-height: 1.6;\r\n        word-wrap: break-word;\r\n        box-sizing: border-box;\r\n        /*background: red;*/\r\n        max-width: 100%;\r\n        \r\n    }\r\n    \r\n\r\n    .markdown-body img{\r\n        display: block;\r\n        margin: 10px auto;\r\n        border: none;\r\n        border-radius: 10px;\r\n        max-width: 100%;\r\n        box-sizing: content-box;\r\n        background-color: #fff;\r\n    }\r\n\r\n    .section {\r\n        background-color: #f0f0f0;\r\n    }\r\n\r\n    #topic-page{\r\n        margin: 10px auto;\r\n    }\r\n\r\n    .section{\r\n        \r\n        padding: 10px!important;\r\n    }\r\n\r\n    h2.topic-title {\r\n        font-size: 16px;\r\n        line-height: 1.7;\r\n        color: #333;\r\n    }\r\n\r\n    .status {\r\n        position: relative;\r\n    }\r\n\r\n    .status>img.avatar {\r\n        width: 50px;\r\n        height: 50px;\r\n        position: absolute;\r\n        top: 50%;\r\n        margin: -25px 0;\r\n        border-radius: 30px;\r\n        border: 1px solid #ccc;\r\n    }\r\n\r\n    .status>div.detail {\r\n    margin-left: 50px;\r\n    p